.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    padding: 0 15px;
    margin: 0 auto;
    width: 100%;

    @media screen and (min-width: $mobile) {
        width: $mobile;
    }

    @media screen and (min-width: $tablet) {
        width: $tablet;
    }

    @media screen and (min-width: $desktop) {
        width: $desktop;
    }

    @media screen and (min-width: $large-desktop) {
        width: $large-desktop;
    }
}